.popup__container {
  background: #fff;
  -webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  margin: auto;
  width: 430px;
  position: relative;
}

@media screen and (max-width: 768px) {
  .popup__container {
    width: 282px;
  }
}
